import { ChatContext } from "@rp/react/common/hooks"
import { Fade } from "@rp/react/web/mui/core"
import { ChatContextCard } from "./ChatContextCard"
import { ChatSuggestions } from "./ChatSuggestions"

type Props = {
  context: ChatContext["context"]
  isChatEmpty: boolean
}
const ChatContextDisplay = ({ context, isChatEmpty }: Props) => {
  const { title, summary, originSources } = context

  return (
    <Fade in>
      <div>
        <ChatContextCard sources={originSources} title={title} summary={summary} />
        <ChatSuggestions isChatEmpty={isChatEmpty}>
          What would you like to know about {title}?
        </ChatSuggestions>
      </div>
    </Fade>
  )
}

export { ChatContextDisplay }
